

export default {

    name: "ApiAuth",

    namespaced: true,

    state: {},

    getters: {},

    mutations: {},

    actions: {


        login(context, param) {

            var username = param.username;
            var password = param.password;
            var on_ok = param.on_ok;
            var on_error = param.on_error;

            var method = 'post';
            var type = 'Auth';
            var id = null;

            var data = {
                auth: {
                    account: username,
                    secret: password,
                    mechanism: 'password',
                }
            };

            rest.execute({ method, type, id, data, on_error, on_ok });

        }


    },

};
