
<style scoped>
.nav-bar {
  background: darkgreen;
}

.logo {
  color: gray;
}

.logo-image {
  height: 52px;
}

.avatar-small {
  width: 48px;
  border-radius: 24px;
}
</style>


<template>
  <div class="nav-bar">
    <el-menu
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >
      <el-menu-item index="home">
        <template slot="title">
          <img class="logo logo-image" src="./../../public/logo-text.png">
        </template>
      </el-menu-item>

      <el-menu-item index="login">登录</el-menu-item>
      <el-menu-item index="signup">注册</el-menu-item>
    </el-menu>
  </div>
</template>

<script>
import SessionInfoBox from "./SessionInfoBox";

export default {
  name: "NavBar",
  components: {
    SessionInfoBox
  },

  data() {
    const menu = {
      home: "/starting",
      login: "/login",
      signup: "/signup",
      "": "#"
    };
    return { menu };
  },

  methods: {
    handleSelect(index) {
      var url = this.menu[index];
      if (url == null) {
        return;
      } else {
        this.$router.push(url);
      }
    }
  }
};
</script>
