
import axios from 'axios'
// import { callbackify } from 'util';


var debug = null;
var rest_api_url_debug = '/api/';
var rest_api_url_release = 'https://star.bitwormhole.com/api/';



function getRestApiUrl(context) {

    if (debug == null) {
        var config = context.rootState['configuration'];
        debug = config.devtools;
        window.console.log("debug_mode: " + debug);
    }
    var rest_api_url = debug ? rest_api_url_debug : rest_api_url_release;
    return rest_api_url;
}


function makeUrl(context, params) {
    var id = params.id;
    var type = params.type;
    var parameters = params.parameters;

    var url = getRestApiUrl(context) + type;
    if (id != null) { url = url + '/' + id; }

    if (parameters != null) {
        var sep = '?';
        var query = '';
        for (var name in parameters) {
            var value = parameters[name];
            query = query + sep + name + '=' + value;
            sep = '&';
        }
        if (query.length > 0) { url = url + query; }
    }

    return url;
}


function sendRestRequest(context, params) {

    var on_error = params.on_error;
    var on_ok = params.on_ok;
    var method = params.method;
    var data = params.data;
    var url = makeUrl(context, params);
    var task = null;

    if ("GET" == method) {
        task = axios.get(url);

    } else if ("POST" == method) {
        task = axios.post(url, data);

    } else if ("PUT" == method) {
        task = axios.put(url, data);

    } else if ("DELETE" == method) {
        task = axios.delete(url);

    } else {
        throw new Exception("unsupported method: " + method);
    }

    task.then((resp) => {

        var result = makeRestResultOK(resp);
        callback(on_ok, result);

    }).catch((resp) => {

        var result = makeRestResultError(resp);
        context.dispatch('error/handle', result, { root: true });
        callback(on_error, result);

    });

}

function makeRestResultOK(resp) {
    return resp.data;
}

function makeRestResultError(resp) {
    return resp.data;
}


function callback(fn, result) {
    if (fn != null) {
        fn(result);
    }
}


export default {

    name: "rest",

    namespaced: true,

    state: {},

    getters: {},

    mutations: {},

    actions: {

        get(context, params) {
            params.method = 'GET';
            sendRestRequest(context, params);
        },

        post(context, params) {
            params.method = 'POST';
            sendRestRequest(context, params);
        },

        put(context, params) {
            params.method = 'PUT';
            sendRestRequest(context, params);
        },

        delete(context, params) {
            params.method = 'DELETE';
            sendRestRequest(context, params);
        },

    },

};
