
<style scoped>
.nav-bar {
  background: darkgreen;
}

.logo {
  color: gray;
}

.logo-image {
  height: 52px;
}

.avatar-small {
  width: 38px;
  border-radius: 24px;
}
</style>

<template>
  <div class="nav-bar">
    <el-menu
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >
      <el-menu-item index="home">
        <template slot="title">
          <img class="logo logo-image" src="./../../public/logo-text.png" />
        </template>
      </el-menu-item>

      <el-menu-item index="my.reponet">云仓库</el-menu-item>
      <el-menu-item index="my.repository">本地仓库</el-menu-item>
      <el-menu-item index="my.host">设备</el-menu-item>

      <el-submenu index="99">
        <template slot="title">
          <img class="avatar-small" src="/images/avatar1.png" />
          {{session.displayName}}
        </template>
        <el-menu-item index="session.email">  ID: {{session.email}}  </el-menu-item>
        <el-menu-item index="session.settings">设置</el-menu-item>
        <el-menu-item index="session.exit">退出</el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
// import SessionInfoBox from "./SessionInfoBox";

export default {
  name: "NavBar",
  components: {},

  computed: {
    session() {
      return this.$store.getters["CurrentSession/session"];
    }
  },

  data() {
    const menu = {
      home: "/starting",
      "my.reponet": "/reponet",
      "my.repository": "/repository",
      "my.host": "/device",
      "session.settings": "/settings",
      "session.exit": "/logout",
      "": "#"
    };
    return { menu };
  },

  methods: {
    handleSelect(index) {
      var url = this.menu[index];
      if (url == null) {
        return;
      } else {
        this.$router.push(url);
      }
    }
  }
};
</script>
