import Vue from 'vue'
import Vuex from 'vuex'

import ApiAuth from './modules/ApiAuth'
import ApiCreateAccount from './modules/ApiCreateAccount'
import ApiResetPassword from './modules/ApiResetPassword'
import ApiCurrentSession from './modules/ApiCurrentSession'

import ApiUserDevice from './modules/ApiUserDevice'
import ApiRepoNet from './modules/ApiRepoNet'
import ApiRepository from './modules/ApiRepository'

import error_js from './modules/error.js'
import rest_js from './modules/rest.js'
import { stat } from 'fs'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {

    configuration: {}

  },

  getters: {

    configuration(state) {
      return state.configuration;
    }

  },

  mutations: {

    configuration(state, conf) {
      state.configuration = conf;
    }

  },
  actions: {

  },
  modules: {

    Auth: ApiAuth,
    CreateAccount: ApiCreateAccount,
    ResetPassword: ApiResetPassword,
    CurrentSession: ApiCurrentSession,

    UserDevice: ApiUserDevice,
    RepoNet: ApiRepoNet,
    Repository: ApiRepository,

    rest: rest_js,
    error: error_js
  }
})
