

<template>
  <div class="nav-bar">
    <div v-show="display.current==display.anonym">
      <BarForAnonym />
    </div>
    <div v-show="display.current==display.user">
      <BarForUser />
    </div>
  </div>
</template>

<script>
import BarForAnonym from "./NavBarForAnonym";
import BarForUser from "./NavBarForUser";

export default {
  name: "NavBar",
  components: {
    BarForAnonym,
    BarForUser
  },

  data() {
    return {
      display: {
        current: 0,
        user: 1,
        anonym: 2
      }
    };
  },

  created() {
    this.displayUserMenu(true);
    this.refresh();
  },

  methods: {
    refresh() {
      var self = this;
      var on_ok = resp => {
        var session = resp.currentSession;
        self.displayUserMenu(session.authenticated);
      };
      this.$store.dispatch("CurrentSession/refresh", { on_ok });
    },

    displayUserMenu(for_user) {
      this.display.current = for_user ? this.display.user : this.display.anonym;
    }
  }
};
</script>
