


export default {

    name: "Auth",

    namespaced: true,

    state: {},

    getters: {},

    mutations: {},

    actions: {


        login(context, param) {

            var name = param.username;
            var secret = param.password;
            var on_ok = param.on_ok;
            var on_error = param.on_error;
            var mechanism = 'PASSWORD';

            var type = 'Auth';
            var id = null;

            var data = {
                auth: { name, secret, mechanism }
            };

            context.dispatch('rest/post', { type, id, data, on_error, on_ok }, { root: true });

        }


    },

};
