import { stat } from "fs";

export default {

    name: "ApiAuth",

    namespaced: true,

    state: {

        list: [],

    },

    getters: {

        list(state) {
            return state.list;
        },

    },

    mutations: {

        updateList(state, res) {
            state.list = res.net.list;
        }

    },

    actions: {

        refresh(context, param) {

            var id = null;
            var type = 'RepoNet';

            var on_ok = res => {
                context.commit('updateList', res);
            };

            context.dispatch('rest/get', { id, type, on_ok }, { root: true });
        }

    },

};
