

function invoke(fn, param) {
    if (fn != null) { fn(param); }
}

export default {

    name: "CurrentSession",

    namespaced: true,

    state: {

        session: {}
    },

    getters: {

        session(state) {
            return state.session;
        }

    },

    mutations: {

        update(state, session) {
            state.session = session;
        },

    },

    actions: {

        refresh(context, param) {

            var type = 'CurrentSession';
            var id = null;
            var on_ok = res => {
                context.commit('update', res.currentSession);
                invoke(param.on_ok, res);
            };
            context.dispatch('rest/get', { type, id, on_ok }, { root: true });
        },


        logout(context, param) {
            var type = 'CurrentSession';
            var id = 'current';
            var on_ok = param.on_ok;
            var on_error = param.on_error;
            context.dispatch('rest/delete', { type, id, on_error, on_ok }, { root: true });
        },

    },

};
